.bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: #fdd9d1;
  }
  
  .container {
      position: flex;
      height: 600px;
  }
  
  .texts {
    color: #ff5152;
    font-family: Inter;
    font-size: 20px;
    font-weight: 700;
    line-height: normal;
    text-align: center;
  }
  
  .welcomecontainer {
      position: absolute;
      margin-top: 100px;
     margin-left: 100px;
    width: 360px;
    height: 416px;
  
  }
  
  .logincontainer{
    position: absolute;;
  
    margin-left: 810px;
      width: 525px;
      height: 600px;
      border-radius: 10px;
      background: #FFF;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  }
  
  .heading {
    color: #ff5152;
    font-family: "Holtwood One SC";
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
  }
  
  .heading2 {
      padding-top: 30px;
      padding-bottom: 15px;
      color: #000;
  font-family: Inter;
  font-size: 22px;
  font-weight: 800;
  line-height: normal;
    }
  
  .inputcontainer{
      width: 402px;
  height: 45px;
  border-radius: 5px;
  border: 2px solid rgba(255, 81, 82, 0.52);
  background: rgba(255, 255, 255, 0.60);
  padding-left: 20px;
 margin-bottom: 18px;
  
  }

  .inputcontainer_id {
    flex: 1; /* 입력 필드가 버튼과 함께 공간을 공유 */
    height: 45px;
    width: 290px;
    border-radius: 5px;
    border: 2px solid rgba(255, 81, 82, 0.52);
    background: rgba(255, 255, 255, 0.6);
    padding-left: 20px;
  }
  .checkboxGroup {
    display: flex; /* 체크박스들을 한 줄에 배치 */
    align-items: center; /* 수직 정렬 */
    gap: 20px; /* 체크박스 간 간격 */
    margin-bottom: 18px; /* 아래 여백 */
  }
  
  .checkboxLabel {
    display: flex;
    align-items: center;
    font-size: 14px; /* 텍스트 크기 */
    color: #ff5152; /* 텍스트 색상 */
    font-weight: 600;
    cursor: pointer;
  }
  
  .checkbox {
    margin-right: 5px; /* 체크박스와 텍스트 간 간격 */
    width: 18px;
    height: 18px;
    color:#ff5152;
    accent-color: #ff5152; /* 체크박스 체크 색상 */
  }
  .button2 {
    width: 100px; /* 버튼 크기 고정 */
    height: 45px; /* 입력 필드와 높이를 맞춤 */
    font-size: 14px; /* 텍스트 크기 */
    font-weight: 700; /* 텍스트 굵기 */
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    margin: 0; /* 버튼의 기본 마진 제거 */
  }

.inlineGroup {
    display: flex; /* 한 줄에 배치 */
    align-items: center; /* 수직 정렬 */
    gap: 10px; /* 요소 간 간격 */
    margin-bottom: 18px; /* 아래쪽 여백 */
  }
  
  .description {
    margin: 15px 0 25px;
    font-size: 18px;
    line-height: 1.61;
    color: #878787;
    text-align: left;
    letter-spacing: normal;
  }
  
  .figure {
    flex: 1 1;
    text-align: center;
    margin-top: 41px;
  }
  
  .figure img {
    width: 100%;
    max-width: 257px;
  }
  
  .button1 {
    display: inline-block;
    width: 402px; /* 부모 컨테이너에 맞춤 */
    height: 42px; /* 버튼 높이 설정 */
    font-size: 16px; /* 텍스트 크기 */
    font-weight: 700; /* 텍스트 굵기 */
    text-align: center; /* 텍스트 가운데 정렬 */
    border-radius: 5px; /* 버튼의 둥근 모서리 */
    cursor: pointer; /* 마우스 포인터 변경 */
  }
  


  .formGroup {
    display: flex;
    flex-direction: column; /* 세로로 정렬 */
    align-items: start;
margin-left: 54px   ;
  }
  .text2{
    color: #FF5152;
  font-family: Inter;
  font-size: 13px;
  font-weight: 400;
  line-height: normal;
  display: inline;
  }
  .text3{
    color: #FF5152;
  font-size: 13px;
  font-weight: 800;
  margin-left: 12px;
  
  }
  
  @media (max-width: 840px) {
    .container {
      flex-direction: column; /* 세로로 배치 */
      align-items: center;
      justify-content: center;
      height: auto; /* 높이 자동 조정 */
      margin: 20px auto;
    }
  
    .welcomecontainer {
      display: none; /* 화면이 작아지면 숨김 */
    }
  
    .logincontainer {
      margin: 10px auto;
      width: 70%; /* 화면 너비의 90%로 설정 */
      padding: 15px; /* 내부 여백 추가 */
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* 그림자 크기 줄임 */
    }
  }
  
  